import {
  Box,
  Card,
  Grid,
  styled,
  useMediaQuery,
  useTheme,
} from "@mui/material";
import FlexBox from "components/flexbox/FlexBox";
import FlexRowAlign from "components/flexbox/FlexRowAlign";

import { H3, H5, Small } from "components/Typography";
import { ChevronRight } from "lucide-react";

import { useNavigate } from "react-router-dom";

export default function Intro({}) {
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down("sm"));
  const isLaptop = useMediaQuery(theme.breakpoints.down("lg"));
  const navigate = useNavigate();

  const StyledCard = styled(Card)(({ theme }) => ({
    flex: 1,
    border: `1px solid ${theme.palette.divider}`,
    display: "flex",
    flexDirection: "column",
    justifyContent: "center",
    alignItems: "center",
    width: isLaptop ? "100%" : "800px",

    [theme.breakpoints.down("sm")]: {
      "& > .MuiBox-root": {
        paddingBottom: 0,
      },
    },
  }));

  return (
    <>
      <StyledCard>
        <Grid
          container
          sx={{
            height: "100%",
            width: "100%",
          }}
        >
          <Grid
            onClick={() => navigate("/about")}
            container
            item
            xs={12}
            sm={6}
            lg={4.5}
            justifyContent={"center"}
            alignItems={"center"}
            backgroundColor={theme.palette.primary.lightGrey200}
            flexDirection={"column"}
            sx={{ cursor: "pointer", minHeight: "400px" }}
          >
            <img
              src={"/static/logos/forsea-big.png"}
              alt="logo"
              style={{
                height: "80px",
              }}
            />

            <FlexRowAlign
              mt={2}
              sx={{
                background: "#ffffff",
                padding: "10px 20px",
                borderRadius: "100px",
                border: "1px solid #E1E1E1",
                boxShadow: "2px 2px 16px rgba(0, 0, 0, 0.05)",
              }}
            >
              <H3 sx={{ color: theme.palette.primary.darkBlue }}>한국어</H3>
              <ChevronRight color={theme.palette.primary.darkBlue} size={20} />
            </FlexRowAlign>
          </Grid>

          <Grid
            onClick={() => navigate("/about")}
            container
            item
            xs={12}
            sm={6}
            lg={4.5}
            justifyContent={"center"}
            alignItems={"center"}
            flexDirection={"column"}
            sx={{ cursor: "pointer", minHeight: "400px" }}
          >
            <img
              src={"/static/logos/fukunari-big.png"}
              alt="logo"
              style={{
                height: "80px",
              }}
            />

            <FlexRowAlign
              mt={2}
              sx={{
                background: "#ffffff",
                padding: "10px 20px",
                borderRadius: "100px",
                border: "1px solid #E1E1E1",
                boxShadow: "2px 2px 16px rgba(0, 0, 0, 0.05)",
              }}
            >
              <H3 sx={{ color: theme.palette.primary.darkBlue }}>日本語</H3>
              <ChevronRight color={theme.palette.primary.darkBlue} size={20} />
            </FlexRowAlign>
          </Grid>

          <Grid
            onClick={() => navigate("/about")}
            container
            item
            xs={12}
            sm={12}
            lg={3}
            backgroundColor={theme.palette.primary.lightGrey200}
            justifyContent={"center"}
            alignItems={"center"}
            flexDirection={"column"}
            sx={{ cursor: "pointer", minHeight: "400px" }}
          >
            <FlexRowAlign
              mt={2}
              sx={{
                background: "#ffffff",
                padding: "10px 20px",
                borderRadius: "100px",
                border: "1px solid #E1E1E1",
                boxShadow: "2px 2px 16px rgba(0, 0, 0, 0.05)",
              }}
            >
              <H3 sx={{ color: theme.palette.primary.darkBlue }}>English</H3>
              <ChevronRight color={theme.palette.primary.darkBlue} size={20} />
            </FlexRowAlign>
          </Grid>
        </Grid>
      </StyledCard>
    </>
  );
}
